import { useEffect, useState } from "react";

const useDebouncedInput = (letter, delayTime) => {
  const [debouncedLetter, setDebouncedLetter] = useState(letter);

  useEffect(() => {
    const delayTimeHandler = setTimeout(() => {
      setDebouncedLetter(letter);
    }, delayTime);

    return () => {
      clearTimeout(delayTimeHandler);
    };
  }, [letter, delayTime]);

  return debouncedLetter;
};
export default useDebouncedInput;
