import React from "react";
import "./wordSuggestedList.css";

const WordSuggestedList = ({
  inputValue,
  currentIndex,
  displaySuggestedList,
  suggestedWords,
  onItemClick,
}) => {
  return (
    <>
      {displaySuggestedList && (
        <section className="suggested-words-container">
          <ul className="suggested-words-list">
            {suggestedWords.map((data, index) => (
              <li
                key={index}
                onMouseDown={() => onItemClick(data.word)}
                className={`suggested-words-item ${
                  currentIndex === index ? "active" : null
                }`}
              >
                {data.word.substr(0, inputValue.length) === inputValue ? (
                  <p className="word-letters">
                    <strong className="word-letters-highlight">
                      {data.word.substr(0, inputValue.length)}
                    </strong>
                    {data.word.substr(inputValue.length, data.word.length)}
                  </p>
                ) : (
                  <p className="word-text">{data.word}</p>
                )}
              </li>
            ))}
          </ul>
        </section>
      )}
    </>
  );
};

export default WordSuggestedList;
