import React, { useState, useEffect } from "react";
import { toast } from "react-toastify";

import useDebouncedInput from "./../../hooks/debounceInput";
import { findSearchedWord } from "./../../services/searchService";
import WordSuggestedList from "../WordSuggestedList/wordSuggestedList";
import SearchHistory from "../SearchHistory/searchHistory";

import "./searchBar.css";

const SearchBar = () => {
  //searchWord: The input value for search.
  const [searchWord, setSearchWord] = useState("");
  //debouncedSearchWord is a custome hook which uses for holding the entered letters for 500ms,
  //if user doesn't type anymore during this time, it will send the letters to API for searching.
  const debouncedSearchWord = useDebouncedInput(searchWord, 500);
  //searchActoin: activate or deactivate search process.
  const [searchActoin, setSearchActoin] = useState(true);
  //suggestedWords: A list of suggested words based on serached letters which is returned by search API.
  const [suggestedWords, setSuggestedWords] = useState([]);
  //displaySuggestedList: Allow show words suggestion list.
  const [displaySuggestedList, setDisplaySuggestedList] = useState(false);
  //currentIndex: The item on suggestedWords which is selected.
  const [currentIndex, setCurrentIndex] = useState(-1);
  //searchedWordsHistory: Return a list of user search history
  const [searchedWordsHistory, setSearchedWordsHistory] = useState([]);

  //it invokes searching by entering letters.
  useEffect(() => {
    if (searchActoin) Searching(debouncedSearchWord);
  }, [debouncedSearchWord, searchActoin]);

  // Show the history of searches by localStorage
  const getSearchesHistory = () => {
    try {
      if (localStorage.getItem("searchHistory")) {
        const searchHistory = JSON.parse(localStorage.getItem("searchHistory"));
        setSearchedWordsHistory(searchHistory);
      }
    } catch (error) {
      toast.error("An error occurred while receiving the search history");
    }
  };

  // call search history
  useEffect(() => {
    getSearchesHistory();
  }, []);

  //For searching letters entered in searchWord and showing suggested words using datamuses API.
  const Searching = async (searchWord) => {
    if (searchWord) {
      try {
        setCurrentIndex(-1);
        setSuggestedWords([]);
        const { data } = await findSearchedWord(searchWord);
        setSuggestedWords(data);
        setDisplaySuggestedList(true);
      } catch (err) {
        setSuggestedWords([]);
        setDisplaySuggestedList(false);
        toast.error(err);
      }
    } else {
      setSuggestedWords([]);
      setDisplaySuggestedList(false);
    }
  };

  //Add searched item including word, word's ID, and serach date/time to search history.
  const addWordToHistory = (word) => {
    let searchGetTime = new Date();
    try {
      let history = {
        id: `${searchGetTime} - ${word.charAt(
          Math.floor(Math.random() * word.length)
        )}${word.charAt(Math.floor(Math.random() * word.length))}${word.charAt(
          Math.floor(Math.random() * word.length)
        )}${Math.floor(Math.random() * 1000) * word.length}`,
        searchedWord: word,
        searchTime:
          searchGetTime.getFullYear() +
          "-" +
          ("0" + (searchGetTime.getMonth() + 1)).slice(-2) +
          "-" +
          ("0" + searchGetTime.getDate()).slice(-2) +
          ", " +
          ("0" + searchGetTime.getHours()).slice(-2) +
          ":" +
          ("0" + searchGetTime.getMinutes()).slice(-2),
      };

      const historyInput = [...searchedWordsHistory];
      historyInput.unshift(history);

      setSearchedWordsHistory(historyInput);

      localStorage.setItem("searchHistory", JSON.stringify(historyInput));
    } catch (error) {
      toast.error(
        "An error occurred while adding a search item to the history."
      );
    }
  };

  // Delete an item of search history by itemID
  const deleteHistoryItem = (itemID) => {
    try {
      const searchHistoryList = [...searchedWordsHistory];
      const filteredHistoryList = searchHistoryList.filter(
        (i) => i.id !== itemID
      );

      localStorage.setItem(
        "searchHistory",
        JSON.stringify(filteredHistoryList)
      );

      setSearchedWordsHistory(filteredHistoryList);
    } catch (error) {
      toast.error("An error occurred while deleting the item from history");
    }
  };

  //Delete all search history
  const deleteAllHistory = () => {
    try {
      if (searchedWordsHistory.length === 0) return;
      setSearchedWordsHistory([]);
      localStorage.setItem("searchHistory", []);
    } catch (error) {
      toast.error("An error occurred while deleting the history");
    }
  };

  //Handle action for clicking input box
  const handleSearchBoxClick = () => {
    if (searchWord.length > 0) {
      setSearchActoin(true);
      setDisplaySuggestedList(true);
    } else {
      setSearchActoin(false);
    }

    setCurrentIndex(-1);
  };

  //Selecting an item from suggested words list
  const handleSuggestedWordItemClick = (word) => {
    setSearchWord(word);
    setSearchActoin(false);
    setDisplaySuggestedList(false);
    addWordToHistory(word);
  };

  //Selecting an item from search history
  const handleHistorySearchedWordClick = (word) => {
    setSearchWord(word);
    setSearchActoin(false);
    setDisplaySuggestedList(false);
  };

  //Add searched item to suggestion list by clicking on search button
  const HandleSearchButtonClick = (e) => {
    e.preventDefault();
    setDisplaySuggestedList(false);
    addWordToHistory(searchWord);
  };

  //handle keydown states for Arrow Up and down, Enter, and Escape keys.
  const handleKeyDown = (e) => {
    let key = e.key;
    switch (key) {
      case "ArrowDown":
        setSearchActoin(false);
        if (!displaySuggestedList) {
          handleSearchBoxClick(null);
        }
        if (currentIndex === suggestedWords.length - 1) {
          setCurrentIndex(0);
          setSearchWord(suggestedWords[0]?.word);
          return;
        }
        if (currentIndex < suggestedWords.length - 1) {
          setCurrentIndex(currentIndex + 1);
          setSearchWord(suggestedWords[currentIndex + 1].word);
        }
        break;
      case "ArrowUp":
        setSearchActoin(false);
        if (!displaySuggestedList) {
          handleSearchBoxClick(null);
        }
        if (currentIndex <= 0) {
          setCurrentIndex(suggestedWords.length - 1);
          setSearchWord(suggestedWords[suggestedWords.length - 1]?.word);
        } else {
          setCurrentIndex(currentIndex - 1);
          setSearchWord(suggestedWords[currentIndex - 1]?.word);
        }
        break;
      case "Enter":
        e.preventDefault();
        setSearchWord(e.target.value);
        setDisplaySuggestedList(false);
        addWordToHistory(e.target.value);
        break;
      case "Escape":
        setSearchWord("");
        setDisplaySuggestedList(false);
        break;
      default:
        setSearchActoin(true);
        break;
    }
  };

  return (
    <section
      className="search-container"
      role="search"
      onBlur={() => setDisplaySuggestedList(false)}
    >
      <form method="get" action="/search" className="search-form">
        <fieldset className="search-fileds">
          <legend className="searchbar-legend">
            Search our website via Soltia search engine
          </legend>
          <button
            type="submit"
            className="search-button searchbar-background"
            title="Search Icon"
            aria-lable="search"
            onClick={HandleSearchButtonClick}
          >
            <i className="fa fa-search"></i>
          </button>
          <label htmlFor="searchBox" className="search-input-lable">
            Type to search
          </label>
          <input
            type="search"
            name="searchBox"
            id="searchBox"
            placeholder="Type to search"
            title="Type to search"
            className="search-input searchbar-background"
            autoFocus
            required
            autoComplete="off"
            value={searchWord}
            onKeyDown={handleKeyDown}
            onChange={(event) => setSearchWord(event.target.value)}
            onClick={handleSearchBoxClick}
          />
        </fieldset>
      </form>
      <WordSuggestedList
        currentIndex={currentIndex}
        displaySuggestedList={displaySuggestedList}
        suggestedWords={suggestedWords}
        inputValue={searchWord}
        onItemClick={handleSuggestedWordItemClick}
      />
      {searchedWordsHistory ? (
        <SearchHistory
          searchedWordsHistory={searchedWordsHistory}
          handleDeleteHistoryItem={deleteHistoryItem}
          handleDeleteHistory={deleteAllHistory}
          onHistoryItemClick={handleHistorySearchedWordClick}
        />
      ) : null}
    </section>
  );
};

export default SearchBar;
