import React from "react";
import "./searchHistory.css";

const SearchHistory = ({
  searchedWordsHistory,
  handleDeleteHistoryItem,
  handleDeleteHistory,
  onHistoryItemClick,
}) => {
  return (
    <section className="search-history">
      <header className="history-header">
        <h2 className="history-title">History</h2>
        <button
          type="button"
          title="Delete all histories"
          className="clear-history"
          onClick={() => handleDeleteHistory()}
        >
          Clear history
        </button>
      </header>
      <section className="history-list-container">
        {searchedWordsHistory.length > 0 ? (
          <ul className="history-pane" aria-label="search-history-list">
            {searchedWordsHistory.map((history) => (
              <li className="history-item" key={history.id}>
                <p
                  className="history-item-word"
                  onMouseDown={() => onHistoryItemClick(history.searchedWord)}
                >
                  {history.searchedWord}
                </p>
                <time className="history-item-date">{history.searchTime}</time>
                <button
                  type="button"
                  className="delete-history-item fa fa-remove"
                  title="Delete"
                  aria-label="delete-history-item"
                  onClick={() => handleDeleteHistoryItem(history.id)}
                ></button>
              </li>
            ))}
          </ul>
        ) : (
          <h3 className="empty-history-text">There is no search history.</h3>
        )}
      </section>
    </section>
  );
};

export default SearchHistory;
